import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { Http, Response, Headers } from '@angular/http';
import { Angular2Csv } from 'angular2-csv/Angular2-csv';
import { ViewAlertsService } from './viewalerts.service';
 import * as _ from "lodash";
// declare const _ = lodash;

declare var $:any;
declare interface Task {
  title: string;
  checked: boolean;
}

@Component({
  selector: 'app-viewalerts',
  templateUrl: './viewalerts.component.html',
  styleUrls: ['./viewalerts.component.css'],
  providers: [ ViewAlertsService ]
})
export class ViewalertsComponent implements OnInit {

  public title;
  public message;
  public image;
  public capturedBy;
  public datecaptured;
  public selectedId;
  public currentUser;
  public suburb;
  public suburbId;
  public usersdeviceid;
  //get the site url
  public siteurl = window.location.origin;

  constructor(private Viewnotificationservice: ViewAlertsService, private location: Location) 
  { this.getNotificationSelectedId(); this.getNotificationById(); this.getCurrentUsers(); }

  ngOnInit() {
    $('#suburbId').hide();
    let role = JSON.parse(localStorage.getItem('role'));
     // console.log("alertsRole",role);

     //if role is equal to communications department then show add button 
    // if(role == "5a93fa58360f060014c2cff1"){
      $("#send").show();
      $("#edit").show();
    // }else{
    //   $("#send").hide();
    //   $("#edit").hide();
    // }
  }

  getNotificationSelectedId() {
// get Selected Id from the Datatable
let NotificationSelectedId = JSON.parse(localStorage.getItem('selectedId'));
    this.selectedId = NotificationSelectedId;
        console.log("Notification Id:",this.selectedId);

}

  getNotificationById() {
 let data = this.Viewnotificationservice.getNotificationById(this.selectedId).subscribe(( response: Response) => {
       
         const str = response.json();
          this.title = str.title;
          this.message = str.message;
          //this.image = str.image;
          this.datecaptured = str.datecaptured;
          this.capturedBy = str.capturedBy;

         
         if(str.image === ""){
            this.image ="../../assets/img/image_placeholder.jpeg"
         }else{
            this.image= str.image;
         }

           this.suburbId = str.suburb;
           this.suburb = str.notification_suburb;
          if(this.suburb === undefined || this.suburb === ""){
            this.suburb ="All users";
            this.suburbId = "5a0715833e67ef00123b588c";
         }else
         if(this.suburb === "All"){
            this.suburb = "All users";
            this.suburbId = "5a0715833e67ef00123b588c";
         }else{
           this.suburb= str.notification_suburb.name;
           this.suburbId = str.suburb;
         }
         localStorage.setItem('editedSendToId', JSON.stringify(this.suburbId));

        
          console.log("NotificationByIdData", str);
      });
}

  closeView(){this.location.back();}

  ngAfterViewInit(){
      //  Activate the tooltips
      $('[rel="tooltip"]').tooltip();
      // this.postToDevice();
  }

    Onclick(){
        localStorage.setItem('editedId', JSON.stringify(this.selectedId));
        localStorage.setItem('editedTitle', JSON.stringify(this.title));
        localStorage.setItem('editedMessage', JSON.stringify(this.message));
        localStorage.setItem('editedSendTo', JSON.stringify(this.suburb));

        localStorage.setItem('editedImage', this.image);
        console.log("Image: "+this.image);
    }


     //pushing notifications to the device
    postToDevice(){ 

          var subs =   $('#suburbId').val();

          if(subs == undefined){
            alert("The suburb is empty");
          }else
              //if suburb is equal All user
            if(subs == "5a0715833e67ef00123b588c"){
                //POST TO ALL USERS ON THE TSHWANE USER TABLE
               this.Viewnotificationservice.getAllUserSuburbs().subscribe(
                   data =>{
                     const str = data.json(); 
                      console.log("str",str);
                                 
                      var results=[];
                         for (var i=0; i < str.length; i++){
                           var users = str[i].deviceid;
                            results.push(users);
                      }
                      var result =  _.remove(results, undefined);
                                         
                     localStorage.setItem('usersdeviceid', JSON.stringify(result)); 

              });
                  this.usersdeviceid = JSON.parse(localStorage.getItem('usersdeviceid'));
                  console.log("data: ", this.usersdeviceid);

                  let newArrObj = [];
                  let arrDeviceIds = this.usersdeviceid;
                 
                  for(var i =0; i < arrDeviceIds.length;i++){
                        newArrObj.push(
                                      {
                                        "to": arrDeviceIds[i],
                                        "title": "notifications: "+  this.title,
                                        "body": this.message,
                                        "dataObject":{"title": this.title,"message":this.message,"datecaptured":this.datecaptured,"type":"notifications","notificationId": this.selectedId} 
                                      }
                                    );
                   }
                   console.log("results",newArrObj);
                  for(var d =0; d < newArrObj.length;d++){

                     this.Viewnotificationservice.postToDevice(newArrObj[d])
                          .subscribe(
                                (response: Response)=> {
                                
                                  console.log("post To Device:",response);
                                 // console.log("Form values:",newArrObj);
                                  
                                }); 
                  } 
                    alert("Notification sent successfully");
                    //  this.location.back();  

            }else{
              //POST TO USERS ON THE TSHWANE USER TABLE THAT MATCHES THE SUBURB
                 var suburbValue =   $('#suburbId').val();    
                 this.Viewnotificationservice.getAllUserSuburbs().subscribe(
                      data =>{
                        const str = data.json(); 
                          let user =<any>{};
                          user =  _.filter(str, { suburb: suburbValue });//filtering where suburb is equal to the entered one
                          console.log("users: ", user);
                          if(user.length == 0){
                             alert("No user in the specified suburb");
                              // this.location.back();
                          }else{

                               var results=[];
                               for (var i=0; i < user.length; i++){
                                  var subUsers = user[i].deviceid;
                                  results.push(subUsers);
                               }
                                var result =  _.remove(results, undefined);
                                console.log("result: ", result);

                                let newArrObj = [];
                                let arrDeviceIds = result;
                                for(var i =0; i < arrDeviceIds.length;i++){
                                   newArrObj.push(
                                                     {
                                                        "to": arrDeviceIds[i],
                                                         "title": "notifications: "+  this.title,
                                                         "body": this.message,
                                                         "dataObject":{"title": this.title,"message":this.message,"datecaptured":this.datecaptured,"type":"notifications","notificationId": this.selectedId} 
                                                      }
                                                  );
                                }
                                  console.log("results",newArrObj);
                                  for(var d =0; d < newArrObj.length;d++){

                                       this.Viewnotificationservice.postToDevice(newArrObj[d])
                                        .subscribe(
                                             (response: Response)=> {
                                        
                                              console.log("post To Device:",response); 
                                        }); 
                                          

                                  }
                                   alert("Notification sent successfully");
                                    // this.location.back();


                          }

                 });
          }//the other else part

    }
    //send and update the notification
     sendNotification(){

       // $('button[type=submit]').attr('disabled', 'disabled'); 
      
       var formModel = Object.assign({"title":this.title},{"is_sent":true}, {"message":this.message},{"sentBy":this.currentUser},{"capturedBy":this.currentUser},{"image":this.image});
        
        this.Viewnotificationservice.putNotifications(this.selectedId,formModel)
        .subscribe(
              (response: Response)=> {
                
                     this.postToDevice();//push notification to a phone.
                     //this.clearFormData();
                     // this.closeView();
                     //alert("Notification sent successfully"),
                     console.log("the response is:",response);
             },
              (error) => { 
                 if (error.status == 401) {
                       alert("Authentication reguired");
                  } else if(error.status == 422) {
                         alert("Notification was not sent.");
                       //  this.clearFormData();
                         console.log("the error is:",error);
                         console.log("Form Data", formModel)
                  } else{
                         alert("Notification was not sent");
                         //this.clearFormData();
                         console.log("the error is:",error);
                         console.log("Form Data", formModel);

                      }
              });
        

      }
       //get user details
    getCurrentUsers()  
     {  
        let data = this.Viewnotificationservice.getUserDetails().subscribe(( response: Response) => {
          
        const str = response.json(); 
        this.currentUser = str.fullname;
        console.log("Username:",this.currentUser)

      });  
         
    }


}



