import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions,RequestMethod} from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';


@Injectable()
export class ChangeStatusService 
{
	 constructor(private http: Http) {}  

   public getStatus()
    {
        let userRegion = JSON.parse(localStorage.getItem('userRegion'));
        // console.log("userRegionChange: ", userRegion);
        //if nodal point or war room show assigned else hide it
        if(userRegion == "59ddf85cb466770012a44bb6" || userRegion == "59ddf802b466770012a44bb0"){
             let url: string = 'https://tshwanesafetyapi.herokuapp.com/api/Statuses?filter={"where":{"name":{"nlike":"New.*"}}}';
             return this.http.get(url);
         }else{
            let url: string = 'https://tshwanesafetyapi.herokuapp.com/api/Statuses?filter={"where": {"name": {"nin": ["New", "Assigned"]}}}';
             return this.http.get(url);

         }
    }
     public getRegion()
    {
        
        let url: string = 'https://tshwanesafetyapi.herokuapp.com/api/Regions?filter={"where": {"name": {"nin": ["USSD", "Mobile Application"]}}}&[order]=name ASC';
        return this.http.get(url);
    }

     public getDepartment()
    {
        
        let url: string = 'https://tshwanesafetyapi.herokuapp.com/api/Departments?filter[order]=name%20ASC';
        return this.http.get(url);
    }

    public getIncidentById(incidentId: any)  
     {       
          let url: string = 'https://tshwanesafetyapi.herokuapp.com/api/Incidents/'+ incidentId +'?filter={"include":["incident_channel","incident_status","incident_category","incident_subcategory","incident_region","image"]}';
          return this.http.get(url);
        
    }
     
    public postIncidentHistory(incidentHistoryData: any)
    {
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ headers: headers });
        let body = JSON.stringify(incidentHistoryData);

        return this.http.post('https://tshwanesafetyapi.herokuapp.com/api/histories/', body , options).map((res: Response) => res.json());

    }
     public getDeviceId(userId: any)  
     {       
          let url: string = 'https://tshwanesafetyapi.herokuapp.com/api/TshwaneUsers/'+userId;
          return this.http.get(url);
        
    }
     public postToDevice(datavalues: any)
    {
         let headers = new Headers();
         headers.set('Content-Type', 'application/json' );
         let options = new RequestOptions( {headers: headers });
         let body = datavalues;
         // console.log("the error is:", body);
         
         return this.http.post('https://tshwanesafetyapi.herokuapp.com/api/Incidents/sendNotification', body , options)
         .map(
            (res: Response) => res.json()  
            );
    
    }

    
    //post the captured incidents into the database 
    public postIncidents(id: any, incidentData: any)
    {
        
          let headers = new Headers({ 'Content-Type': 'application/json' });
          let options = new RequestOptions({ headers: headers });
          let body = JSON.stringify(incidentData);

          return this.http.patch('https://tshwanesafetyapi.herokuapp.com/api/Incidents/'+id, body , options).map((res: Response) => res.json());

    }

    
    public getUserDetails(){

         let userDetail = localStorage.getItem('currentUser');
         // console.log("my name",userDetail);
         let userId = JSON.parse(userDetail).userId;
         console.log("userId",userId);
         let access_token = JSON.parse(userDetail).id;
         console.log("access_token",access_token);
         
         let url: string = 'https://tshwanesafetyapi.herokuapp.com/api/TshwaneUsers/'+userId+'?access_token='+access_token;
         return this.http.get(url);

    }
}





