import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';


@Injectable()
export class ViewIncidentsService 
{
	 constructor(private http: Http) {}  

   
     public getIncidentById(incidentId: any)  
     {       
          let url: string = 'https://tshwanesafetyapi.herokuapp.com/api/Incidents/'+ incidentId +'?filter={"include":["incident_channel","incident_status","incident_category","incident_subcategory","incident_region","image"]}';
          return this.http.get(url);
        
    }
    public getHistoryIncidentById(refnum: any)  
     {       
          let url: string = 'https://tshwanesafetyapi.herokuapp.com/api/histories?filter[where][refNumber]='+refnum;
           console.log("History incidentByIdData2", url );
          return this.http.get(url);
        
    }
     public getHistoryData(refnum: any)  
     {       
          let url: string = 'https://tshwanesafetyapi.herokuapp.com/api/histories?filter[where][refNumber]='+refnum;
         
          return this.http.get(url);
        
    }
}





