 getIncidentById() {
         let data = this.changestatusservice.getIncidentById(this.selectedIncidentsId).subscribe(( response: Response) => {
       
         const str = response.json();
         this.capturedBy = str.capturedBy;
         this.reportedOn = str.reportedOn;
         this.refNumber = str.refNumber;
         this.categoryType = str.incident_subcategory.name;
         this.category = str.incident_category.name;
         this.region = str.region;
         //this.subCategory = str.incident_subcategory.name;
         localStorage.setItem('selectedType', JSON.stringify(this.categoryType));

        console.log("incidentByIdData", str);
        localStorage.setItem('selectedIncidentToUpdateStatus', JSON.stringify(str));
         
        this.tshwaneUserId= str.tshwaneUserId;
        console.log("tshwaneUserId",  this.tshwaneUserId);
        if(this.tshwaneUserId == undefined){
            alert("UserId is empty");
        }else{
            localStorage.setItem('tshwaneUserId', JSON.stringify(this.tshwaneUserId));
            console.log("tshwaneUserIdSuccess",  this.tshwaneUserId);
         }
          
         
       

         });
     }

