import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';


@Injectable()
export class ViewUserService 
{
	 constructor(private http: Http) {}  

   
     public getUsersById(userId: any)  
     {     
     	  let token = JSON.parse(localStorage.getItem('storedToken'));  
          let url: string = 'https://tshwanesafetyapi.herokuapp.com/api/TshwaneUsers/'+ userId +'?filter={"include":["user_region","user_suburb"]}&access_token='+token;
          return this.http.get(url);
        
    }
    
}





