import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';


@Injectable()
export class ViewAlertsService 
{
	 constructor(private http: Http) {}  
    // public getAllUserSuburbs()  
    // {    //?filter={"where": {"deviceid": {"nin": "undefined"}}}
    //      let url: string = 'https://tshwanesafetyapi.herokuapp.com/api/TshwaneUsers?filter={%22include%22:[%22user_region%22,%22user_suburb%22]}';
    //      return this.http.get(url);
    // }
     public getAllUserSuburbs()  
    {    //?filter={"where": {"deviceid": {"nin": "undefined"}}}
         // let url: string = 'https://tshwanesafetyapi.herokuapp.com/api/TshwaneUsers?filter={%22include%22:[%22user_region%22,%22user_suburb%22]}&%22where%22:{%22isDesktopUser%22:%22false%22}';
          let url: string = 'https://tshwanesafetyapi.herokuapp.com/api/TshwaneUsers?filter={%22where%22:{%22isDesktopUser%22:false}}';
         return this.http.get(url);
    }
    
     public getNotificationById(notificationId: any)  
     {       
          let url: string = 'https://tshwanesafetyapi.herokuapp.com/api/Notifications/'+ notificationId+'?filter={"include":["notification_suburb"]}';
          return this.http.get(url);
        
    }
     public putNotifications(id: any,notificationsData: any)
    {
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ headers: headers });
        let body = JSON.stringify(notificationsData);

        return this.http.patch('https://tshwanesafetyapi.herokuapp.com/api/Notifications/'+id, body , options).map((res: Response) => res.json());

    }
    public postToDevice(datavalues: any)
    {
         let headers = new Headers();
         headers.set('Content-Type', 'application/json' );
         let options = new RequestOptions( {headers: headers });
         let body = JSON.stringify(datavalues);
         
         return this.http.post('https://tshwanesafetyapi.herokuapp.com/api/Notifications/sendNotification?', body , options)
         .map(
            (res: Response) => res.json()  
            );
    
    }
    public getUserDetails(){

         let userDetail = localStorage.getItem('currentUser');
         let userId = JSON.parse(userDetail).userId;
         console.log("userId",userId);
         let access_token = JSON.parse(userDetail).id;
         console.log("access_token",access_token);
         
         let url: string = 'https://tshwanesafetyapi.herokuapp.com/api/TshwaneUsers/'+userId+'?access_token='+access_token;
         return this.http.get(url);

    }


    
}





