import { Component, OnInit, OnChanges, AfterViewInit } from '@angular/core';
import { Location } from '@angular/common';
import { Http, Response, Headers } from '@angular/http';
import { Angular2Csv } from 'angular2-csv/Angular2-csv';
import { ViewHistoryService } from './viewhistory.service';
declare var $:any;
@Component({
  selector: 'app-viewhistory',
  templateUrl: './viewhistory.component.html',
  styleUrls: ['./viewhistory.component.css'],
  providers: [ ViewHistoryService ]
})
export class ViewhistoryComponent implements OnInit {
 
 public data;
 public refnum;
 public selectedHistoryId;
 public status_history;
 public refNumber;
 public departmentHandedover;
 public assignedRegion;
 public respondingVehicle;
 public respondingOfficer;
 public supervisor;
 public notes;
 public dateCaptured;
 public capturedBy;
 public responseTime;
 public reportedOn;
 public siteurl = window.location.origin;

 public arNumber;
 public vehicleRegNumber;
 public noOfPeopleInvolved;
 public accidentTime;



  constructor(private viewhistoryservice: ViewHistoryService, private location: Location) { this.getIncidentSelectedIds(); this.getHistoryIncidentById();}

  ngOnInit() {
    
     $('#index').hide();
  }

  getIncidentSelectedIds() {
        // get Selected Id from the Datatable
        this.selectedHistoryId  = JSON.parse(localStorage.getItem('selectedHistoryId'));
         console.log("History Id:",this.selectedHistoryId);
       
    }

  getHistoryIncidentById() 

    {

       // var index = localStorage.getItem('index')
      //     console.log("index", index );
         console.log("History id", this.selectedHistoryId );
         let data = this.viewhistoryservice.getHistoryIncidentById(this.selectedHistoryId).subscribe(( response: Response) => {
             const str = response.json();
             console.log("History Data", str );

             this.refNumber = str.refNumber;
               console.log("refNumber", this.refNumber);

             this.status_history = str.status;
             this.dateCaptured = str.dateCaptured;
             this.capturedBy = str.capturedBy;
             this.notes = str.notes;
                         
             var reportedOnValue = str.reportedOn;
             if(reportedOnValue !== undefined){
               this.reportedOn = str.reportedOn

             }else{
               $('#response').show(); 
             }

           //response time calculations 
         let reportedOn: any = new Date(this.reportedOn);
         let updatedOn: any = new Date( this.dateCaptured);
        
         console.log("reportedOn", reportedOn);
         console.log("updatedOn", updatedOn );
        
         let average = updatedOn.getTime() - reportedOn.getTime();
         console.log("average", average);
        
         //get days
         var days = Math.floor((average) /(1000 * 60 * 60 * 24));
         console.log("days", days);

         //get hours
          let hours = Math.abs(updatedOn.getHours() - reportedOn.getHours());
         console.log("Hours", hours);

         //get Minutes
         let minutes = Math.abs(updatedOn.getMinutes() - reportedOn.getMinutes());
         console.log("Minutes", minutes);

         //get seconds 
         let seconds = Math.abs(updatedOn.getSeconds() - reportedOn.getSeconds());
         console.log("seconds", seconds);

         this.responseTime = days+"-Day(s), " +hours+"-Hour(s), " + minutes+"-Minute(s) and "+seconds+"-Second(s)";
          console.log("responseTime", this.responseTime);
       

             
        if( this.status_history == "Closed"){
            document.getElementById("update").style.display="none";

         }

             var departmentHandedover  = str.departmentHandedover;
       if(departmentHandedover  !== undefined){ 
                 this.departmentHandedover = str.departmentHandedover;
             }else{
               $('#departmentHandedover').hide(); 
           }


           var assignedRegion  = str.assignedRegion;
       if(assignedRegion  !== undefined){ 
                 this.assignedRegion = str.assignedRegion;
             }else{
               $('#assignedRegion').hide(); 
           }


           var respondingVehicle  = str.respondingVehicle;
       if(respondingVehicle  !== undefined){ 
                 this.respondingVehicle = str.respondingVehicle;
             }else{
               $('#respondingVehicle').hide(); 
           }


           var respondingOfficer  = str.respondingOfficer;
       if(respondingOfficer  !== undefined){ 
                 this.respondingOfficer = str.respondingOfficer;
             }else{
               $('#respondingOfficer').hide(); 
           }


           var supervisor  = str.supervisor;
       if(supervisor  !== undefined){ 
                 this.supervisor = str.supervisor;
             }else{
               $('#supervisor').hide(); 
           }


            var arNumber  = str.arNumber;
       if(arNumber  !== undefined){ 
                 this.arNumber = str.arNumber;
             }else{
               $('#arNumber').hide(); 
           }

            var noOfPeopleInvolved  = str.noOfPeopleInvolved;
       if(noOfPeopleInvolved  !== undefined){ 
                 this.noOfPeopleInvolved = str.noOfPeopleInvolved;
             }else{
               $('#noOfPeopleInvolved').hide(); 
           }

            var vehicleRegNumber  = str.vehicleRegNumber;
       if(vehicleRegNumber  !== undefined){ 
                 this.vehicleRegNumber = str.vehicleRegNumber;
             }else{
               $('#vehicleRegNumber').hide(); 
           }


           var accidentTime  = str.accidentTime;
       if(accidentTime  !== undefined){ 
                 this.accidentTime = str.accidentTime;
             }else{
               $('#accidentTime').hide(); 
           }

           
  
         });
    }

    close(){

      this.location.back();
   }


  






}


