import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { AddIncidentsService } from './addincidents.service';
import { Http, Response, Headers } from '@angular/http';

declare var $:any;
declare const google: any;
declare var swal:any;

@Component({
 // moduleId:module.id,
  selector: 'app-addincidents',
  templateUrl: './addincidents.component.html',
  styleUrls: ['./addincidents.component.css'],
  providers: [AddIncidentsService ]
})

export class AddincidentsComponent implements OnInit 
{
  //Success Message
  showSwal(type){
    var context=this
    if(type == 'success-message'){
      swal({
        type: "success",
        title: "Success",
        text: "The incident has been successfully captured!",
        buttonStyling: false,
        confirmButtonClass: "btn btn-primary"
      })
      .then(function() {
                  context.saveIncidents();
                });
      
    }
  }
  
   //variables for storing data  
   categoryName='';
   typeName;
   channelName='';
   regionName='';

   public category;
   public currentUser;
   public userPayNo;
   public ImageUpload;
   public defaultImage;

   postData: string;
 
   //get the site url
   public siteurl = window.location.origin;

   public userSettings3: any = {
    showCurrentLocation: false,
    resOnSearchButtonClickOnly: true,
    inputPlaceholderText: 'Type anything and you will get a location',
    recentStorageName: 'componentData3'
    };
    public componentData1: any = '';

     
   constructor(private _addService: AddIncidentsService, private location: Location) {
        this.getCategories();
        this.getChannel();
        this.getRegion();
        this.getCurrentUsers();
        // this.getAddress();
        
     }

   ngOnInit(){ 
      $("#testLat").hide();
      $("#testLot").hide();
      //password.
      $("#mobileno").keyup(function() 
      {
        $("#mobileno").val(this.value.match(/[0-9]*/));
      });

     
     var map = new google.maps.Map(document.getElementById('geomap'), {
          center: {lat: -25.731340, lng:  28.218370},
          zoom: 13,
          streetViewControl: true,
          scaleControl: true,
          rotateControl: true,
          panControl: true,
          mapTypeControl: true,
          mapTypeControlOptions: {
            style: google.maps.MapTypeControlStyle.DROPDOWN_MENU,
            position: google.maps.ControlPosition.TOP_RIGHT
          },
          overviewMapControl: true,
          overviewMapControlOptions: {
            opened: true,
            position: google.maps.ControlPosition.TOP_LEFT
          },
          mapTypeId: 'roadmap'
        });

        // Create the search box and link it to the UI element.
        var input = document.getElementById('pac-input');
        var searchBox = new google.maps.places.SearchBox(input);
        map.controls[google.maps.ControlPosition.TOP_RIGHT].push(input);

        // Bias the SearchBox results towards current map's viewport.
        map.addListener('bounds_changed', function() {
          searchBox.setBounds(map.getBounds());
        });

        var markers = [];
        // Listen for the event fired when the user selects a prediction and retrieve
        // more details for that place.
        searchBox.addListener('places_changed', function() {
          var places = searchBox.getPlaces();

          if (places.length == 0) {
            return;
          }

          // Clear out the old markers.
          markers.forEach(function(marker) {
            marker.setMap(null);
          });
          markers = [];

          // For each place, get the icon, name and location.
          var bounds = new google.maps.LatLngBounds();
          places.forEach(function(place) {
            (document.getElementById('testLat') as HTMLInputElement).value = place.geometry.location.lat();
             (document.getElementById('testLot') as HTMLInputElement).value = place.geometry.location.lng();
            if (!place.geometry) {
              console.log("Returned place contains no geometry");
              return;
            }
            var icon = {
              url: place.icon,
              size: new google.maps.Size(71, 71),
              origin: new google.maps.Point(0, 0),
              anchor: new google.maps.Point(17, 34),
              scaledSize: new google.maps.Size(25, 25)
            };

            // Create a marker for each place.
            markers.push(new google.maps.Marker({
              map: map,
              icon: icon,
              title: place.name,
              position: place.geometry.location,
              animation: google.maps.Animation.BOUNCE
            }));

            if (place.geometry.viewport) {
              // Only geocodes have viewport.
              bounds.union(place.geometry.viewport);
            } else {
              bounds.extend(place.geometry.location);
            }
          });
          map.fitBounds(bounds);
            // document.getElementById('test').value = places.geometry.location.lat();
        });   

 }
  
  ngAfterViewInit(){
      //  Activate the tooltips
      $('[rel="tooltip"]').tooltip();
    }

   public getCategories(){
      let data = this._addService.getCategory().subscribe(( response: Response) => {
        const str = response.json();
        this.categoryName = str;

      });

    }

 
      onChange(){
       let categoryId = (document.getElementById( "category" ) as HTMLInputElement).value;
        let data = this._addService.getType(categoryId).subscribe(( response: Response) => {
           const str = response.json();
            this.typeName = str.Subcategories;
            console.log("Subcategories:",this.typeName)
           });
      }
   
    public getChannel(){
      
      let data = this._addService.getChannel().subscribe(( response: Response) => {
        const str = response.json(); 
        this.channelName = str;
        console.log("Data 3:",this.channelName)

      });
    }

    public getRegion(){
     
      let data = this._addService.getRegion().subscribe(( response: Response) => {
        
        const str = response.json();
        this.regionName = str;
        console.log("Data 4",this.regionName)

      });
    }

   //form data variable
    public formModel={};

  saveIncidents(){

    if(this.validate())
    {
         
        $('button[type=submit]').attr('disabled', 'disabled');  

        console.log("this.ImageUpload", this.ImageUpload);
        //  var image = 
        if(this.ImageUpload  == undefined || this.ImageUpload  == ""){   
              this.ImageUpload ="../src/assets/img/image_placeholder.jpg";
           }

        var location = $('#pac-input').val();
        var lat = $("#testLat").val();
        var lot = $("#testLot").val();
        //pushing objects in the list
         let values = Object.assign(this.formModel, {"status":"593c39de022c4aca8604e150"}, {"location":location }, {"lat":lat }, {"lot":lot },{"images":{"imageString":this.ImageUpload}},{"payno":this.userPayNo}, {"capturedBy":this.currentUser});
          //console.log("the error is:",error)
          this._addService.postIncidents(values)
          .subscribe(
                (response: Response)=> {
                  // swal({
                  //   type: "success",
                  //   title: "Success",
                  //   text: "Incident has been succes captured!",
                  //   buttonStyling: false,
                  //   confirmButtonClass: "btn btn-primary"
                  // }).then(),
                  alert("Incident has been stored successfully"),
                  console.log("the response is:",response),
                  console.log("Form values:",values);
                   // this.pushNotification();
                   this.clearFormData();
                   this.closeIncident();

                },
                (error) => { 
                  if (error.status == 401) {
                          alert("Authentication reguired");
                        } else {
                           alert("Incident data is not stored");
                           console.log("the error is:",error);
                           console.log("Form values:",this.formModel);
                           
                        }
                });

      }


  }
  

 
     

  
    //Clear form fields
    clearFormData(){
      
       $('input[type="text"]').val('');
       $('input[type="number"]').val('');
       $('input[type="file"]').val('');
       $('#description').val('');
       $('#channel').val('');
       $('#subCategory').val('');
       $('#region').val('');
       $('#category').val('');
       $('#file').val('');
    }

    //Close form and redirect
    closeIncident(){this.location.back();}

    //get user details
    getCurrentUsers()  
     {  
        let data = this._addService.getUserDetails().subscribe(( response: Response) => {
          
        const str = response.json(); 
        
        this.currentUser = str.fullname;
        this.userPayNo = str.payNumber;
        console.log("Username:",this.currentUser)
        console.log("Userpayno:",this.userPayNo)

      });  
         
    }

    //image upload function 
handleFileSelect(evt){
      var files = evt.target.files;
      var file = files[0];
     console.log("files",files);
     console.log("file",file);
    
    if (files && file) {
        var reader = new FileReader();

        reader.onload =this._handleReaderLoaded.bind(this);

        //reader.readAsBinaryString(file);
        reader.readAsDataURL(file);
    }
  }
  
  _handleReaderLoaded(readerEvt) {
   
     var binaryString = readerEvt.target.result;

       this.ImageUpload= binaryString;
    
            console.log("Image Upload",this.ImageUpload);
     }
 

 //validating the form before saving
      validate()
      {

           
            var category =  $('#category').val();
            var region =  $('#region').val();
            var subCategory =  $('#subCategory').val();
            var channel =  $('#channel').val();
            var description =  $('#description').val();
            var location =  $('#pac-input').val();
            // var reportedBy =  $('#reportedBy').val();
            // var mobileno =  $('#mobileno').val();
            

                     
                  if (category == null || category== "") {
                     alert("Category must be Selected");
                     return false;
                  }else {
                   if (region == null || region == "") {
                           alert("Region must be Selected");
                            return false;
                        }else{
                        if (subCategory== null || subCategory == "") {
                               alert("Type must be Selected");
                                return false;
                            }else{
                            if (channel== null || channel == "") {
                               alert("Channel must be Selected");
                                return false;
                            }else{
                            if (description== null || description == "") {
                               alert("Description must be filled out");
                                return false;
                            }else{
                            if (location== null || location == "") {
                               alert("Location must be filled out");
                                return false;
                            }else{
                               return true;
                            }
                            }
                            }
                            }
                            }
                            }
                            
                            
                           
             
      }





}



