import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';


@Injectable()
export class AddUsersService 
{
     constructor(private http: Http) {}  

   
     public getRegion()
    {
        
        let url: string = 'https://tshwanesafetyapi.herokuapp.com/api/Regions?filter={"where": {"name": {"nin": ["USSD", "Mobile Application"]}}}&[order]=name%20ASC';
        return this.http.get(url);
    }
       public getUserGroups()
    {
        
        let url: string = 'https://tshwanesafetyapi.herokuapp.com/api/UserGroups?filter[order]=name%20ASC';
        return this.http.get(url);
    }
    public postUsers(usersData: any)
    {   

         let userDetail = localStorage.getItem('currentUser');
         let userId = JSON.parse(userDetail).userId;
         console.log("userId",userId);
         let access_token = JSON.parse(userDetail).id;
         console.log("access_token",access_token);
        
          let headers = new Headers({ 'Content-Type': 'application/json' });
          let options = new RequestOptions({ headers: headers });
          let body = JSON.stringify(usersData);

          return this.http.post('https://tshwanesafetyapi.herokuapp.com/api/TshwaneUsers?access_token='+access_token, body , options).map((res: Response) => res.json());

    }
     public getUserDetails(){

         let userDetail = localStorage.getItem('currentUser');
         let userId = JSON.parse(userDetail).userId;
         console.log("userId",userId);
         let access_token = JSON.parse(userDetail).id;
         console.log("access_token",access_token);
         
         let url: string = 'https://tshwanesafetyapi.herokuapp.com/api/TshwaneUsers/'+userId+'?access_token='+access_token;
         return this.http.get(url);

    }
    
    public getSuburbs()
    {
        
        let url: string = 'https://tshwanesafetyapi.herokuapp.com/api/Suburbs?filter={"where": {"name": {"nin": ["All"]}}}&[order]=name ASC';
        return this.http.get(url);
    }


}


