import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { Http, Response, Headers } from '@angular/http';
import { Angular2Csv } from 'angular2-csv/Angular2-csv';
import { ViewUserService } from './viewusers.service';

declare var $:any;

@Component({
  selector: 'app-viewusers',
  templateUrl: './viewusers.component.html',
  styleUrls: ['./viewusers.component.css'],
  providers: [ ViewUserService ]
})
export class ViewusersComponent implements OnInit {
  
  
  public selectedId;
  public payNumber;
  public idNumber;
  public fullname;
  public username;
  public email;
  public mobileno;
  public address;
  public realm;
  public occupation;
  public image;
  public region;
  public role;
  public suburb;
  //get the site url
  public siteurl = window.location.origin;

  constructor( private viewuserservice: ViewUserService, private location: Location) { this.getUserSelectedId(); this.getUsersById();}

  ngOnInit() {
  }

  ngAfterViewInit(){
    //  Activate the tooltips
    $('[rel="tooltip"]').tooltip();
  }

  getUserSelectedId() {
// get Selected Id from the Datatable
let UserSelectedId = JSON.parse(localStorage.getItem('selectedUserId'));
	    	this.selectedId = UserSelectedId;
        console.log("Selected User Id 1:",this.selectedId);

}

	getUsersById() {

       console.log("Selected User Id 2:",this.selectedId);
 let data = this.viewuserservice.getUsersById(this.selectedId).subscribe(( response: Response) => {
       
         const str = response.json();
          this.payNumber = str.payNumber;
          this.idNumber = str.idNumber;
          this.fullname = str.fullname; 
          this.username = str.username;
          this.email = str.email;
          this.mobileno = str.mobileno;
          // this.realm = str.realm;
          this.occupation = str.occupation;
          this.region = str.user_region.name;
          // this.image = str.images.imageString;
          this.address = str.address;
          this.suburb = str.user_suburb.name;

           this.role = str.user_role;
          // console.log("image",  this.image );

         if(this.role  == undefined){   
            this.role ="Not added";
         }else{
            this.role = str.user_role.name;
         }   

           this.image = str.images;
          // console.log("image",  this.image );

         if(this.image  == undefined){   
            this.image ="../src/assets/img/image_placeholder.jpg";
         }else{
            this.image = str.images.imageString;
         }   
          console.log("userByIdData", str);
      });


}
	closeView(){this.location.back();}

}



