import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { Http, Response, Headers } from '@angular/http';
import { Angular2Csv } from 'angular2-csv/Angular2-csv';
import { ViewEnquiryService } from './viewenquiry.service';

declare var $:any;

@Component({
  selector: 'app-viewenquiry',
  templateUrl: './viewenquiry.component.html',
  styleUrls: ['./viewenquiry.component.css'],
  providers: [ ViewEnquiryService ]

})
export class ViewenquiryComponent implements OnInit {
  public data;
  public selectedId;
  public fullNames;
  public contactNumber;
  public notes;
  public datecaptured;
  public capturedBy;
  public enquiry_type;
  public address;

  //get the site url
  public siteurl = window.location.origin;

  constructor( private viewenquiryservice: ViewEnquiryService, private location: Location) { this.getEnquirySelectedId(); this.getEnquiryById(); }

  ngOnInit() {
  }

  ngAfterViewInit(){
    //  Activate the tooltips
    $('[rel="tooltip"]').tooltip();
  }

  getEnquirySelectedId() {
// get Selected Id from the Datatable
let EnquirySelectedId = JSON.parse(localStorage.getItem('selectedId'));
		this.selectedId = EnquirySelectedId;
        console.log("Enquiry Id 1:",this.selectedId);

}
	getEnquiryById() {
      console.log("Enquiry Id 2:",this.selectedId);
 let data = this.viewenquiryservice.getEnquiriesById(this.selectedId).subscribe(( response: Response) => {
       
         const str = response.json();
         this.fullNames = str.fullNames;
         this.contactNumber = str.contactNumber;
         this.notes = str.notes;
         this.datecaptured = str.datecaptured;
         this.capturedBy = str.capturedBy;
         this.enquiry_type = str.enquiry_type.name;
         this.address = str.address;
          console.log("enquiryByIdData", str);
      });


}
	closeView(){this.location.back();}


}



