import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';


@Injectable()
export class AddIncidentsService 
{
     public token;
     constructor(private http: Http) {}  

     public postIncidents(incidentData: any)
    {
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ headers: headers });
        let body = JSON.stringify(incidentData);

          return this.http.put('https://tshwanesafetyapi.herokuapp.com/api/Incidents', body , options).map((res: Response) => res.json());

    }

   public getCategory()
    {
        
        let url: string = 'https://tshwanesafetyapi.herokuapp.com/api/Categories?filter[order]=name%20ASC';
        return this.http.get(url);
    }
     public getType(categoryId: any)
    {
        
        let url: string = 'https://tshwanesafetyapi.herokuapp.com/api/SubCategories/findByCategoryId?categoryId='+ categoryId+'&[order]=name%20ASC';
        return this.http.get(url);
    }
     public getChannel()
    {
        
        let url: string = 'https://tshwanesafetyapi.herokuapp.com/api/Channels?filter={"where": {"name": {"nin": ["USSD", "Mobile App"]}}}&[order]=name ASC';
        return this.http.get(url);
    }
     public getRegion()
    {
        
        let url: string = 'https://tshwanesafetyapi.herokuapp.com/api/Regions?filter={"where": {"name": {"nin": ["USSD", "Mobile Application"]}}}&[order]=name ASC';
        return this.http.get(url);
    }
     public getAddress(location: any)
    {
       // console.log("Location: ", location);
        let url: string = 'http://photon.komoot.de/api/?q='+location+'&osm_id=29367008';
        return this.http.get(url);
    }
    //post the captured incidents into the database 
    // public postIncidents(incidentData: any)
    // {
        
    //       let headers = new Headers({ 'Content-Type': 'application/json' });
    //       headers.append('Access-Control-Allow-Origin', '*');
    //       let options = new RequestOptions({ headers: headers });
    //       let body = JSON.stringify(incidentData);
    //       return this.http.post('https://tshwanesafetyapi.herokuapp.com/api/Incidents?access_token='+this.token, body , options).map((res: Response) => res.json());
    // }
     public postToDevice(datavalues: any)
    {
         return this.http.post('https://onesignal.com/api/v1/notifications', datavalues).map((res: Response) => res.json());

    }
    public getUserDetails(){

         let userDetail = localStorage.getItem('currentUser');
         let userId = JSON.parse(userDetail).userId;
         console.log("userId",userId);
         let access_token = JSON.parse(userDetail).id;
         console.log("access_token",access_token);
         this.token = access_token;

         
         let url: string = 'https://tshwanesafetyapi.herokuapp.com/api/TshwaneUsers/'+userId+'?access_token='+access_token;
         return this.http.get(url);

    }

    
}


