import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { PasswordValidation } from './password-validator.component';
import { AddUsersService } from './addusers.service';
import { Http, Response, Headers } from '@angular/http';


declare var require: any
declare var $:any;
declare interface Task {
  title: string;
  checked: boolean;
}

@Component({
  selector: 'app-addusers',
  templateUrl: './addusers.component.html',
  styleUrls: ['./addusers.component.css'],
  providers: [AddUsersService ]
})

export class AddusersComponent implements OnInit {
    
   public regionName;
   public userGroupName;
   public suburbName;
   public ImageUpload;
   public currentUser;
   public userPayNo;
   public siteurl = window.location.origin;
   // public data;
   

   constructor(private _addService: AddUsersService, private location: Location) 
   {
     this.getRegion();
     this.getSuburbs();
     this.getUserGroups();
   }
   

   ngOnInit() {

   	$('#username_div').hide();

       // password and confrim password validation
       $('#password, #confirmPassword').on('keyup', function () {
       if ($('#password').val() == $('#confirmPassword').val()) {
         $('#divCheckPasswordMatch').html('Matching').css('color', 'green');
       } else 
          $('#divCheckPasswordMatch').html('Not Matching').css('color', 'red');
      });

      // mobile number validation
      $("#mobileno").keyup(function() {
       $("#mobileno").val(this.value.match(/[0-9]*/));
      });

   }

   ngAfterViewInit(){
      //  Activate the tooltips
      $('[rel="tooltip"]').tooltip();

   }

    getSuburbs(){
 
             this._addService.getSuburbs().subscribe(
                data =>{
                  this.suburbName = data.json();
                  console.log("Suburbs", this.suburbName);

              });
        }

     getUserGroups(){
 
             this._addService.getUserGroups().subscribe(
                data =>{
                  this.userGroupName = data.json();
                  console.log("Suburbs", this.userGroupName);

              });
        }

   public getRegion(){

      
      let data = this._addService.getRegion().subscribe(( response: Response) => {
       
        const str = response.json();
        console.log("Data 3", str);
        this.regionName = str;
      });
    }

     //get user details
    getCurrentUsers()  
     {  
        let data = this._addService.getUserDetails().subscribe(( response: Response) => {
          
        const str = response.json(); 
        
        this.currentUser = str.fullname;
        this.userPayNo = str.payNumber;
        console.log("Username:",this.currentUser)
        console.log("Userpayno:",this.userPayNo)

      });  
         
    }

   
    public formModel={};
    saveUsers()
    {
  
        if(this.validate())
        {    

           $('button[type=submit]').attr('disabled', 'disabled');//

            console.log("this.ImageUpload", this.ImageUpload);
           //  var image = 
            if(this.ImageUpload  == undefined || this.ImageUpload  == ""){   
              this.ImageUpload ="../src/assets/img/image_placeholder.jpg";
            }
          
            var username =  $('#email').val();
           // var suburb =  $('#suburb').val();
             console.log("username:",username);
         // pushing objects in the list
           Object.assign(this.formModel, {"isDesktopUser": true}, {"username": username},{"images":{"imageString":this.ImageUpload}});
           
            this._addService.postUsers(this.formModel)
            .subscribe(
                  (response: Response)=> {
                     alert("User has been stored successfully"),
                    console.log("the response is:",response)
                     this.clearFormData();
                     this.closeUsers();
                  },
                  (error) => { 
                    if (error.status == 401) {
                            alert("Authentication reguired");
                          } else {
                             alert("User data is not stored");
                             console.log("the error is:",error);
                             console.log("Form values:",this.formModel);
                             
                          }
                  });
        }

    }

    

    //Clear form fields
    clearFormData(){
      
       $('input[type="text"]').val('');
       $('input[type="number"]').val('');
       $('input[type="password"]').val('');
       $('#description').val('');
       $('#region').val('');
    }

    //Close form and redirect
    closeUsers(){this.location.back();
     }


      //image upload function 
  handleFileSelect(evt){
      var files = evt.target.files;
      var file = files[0];
     console.log("files",files);
     console.log("file",file);
    
    if (files && file) {
        var reader = new FileReader();

        reader.onload =this._handleReaderLoaded.bind(this);

        //reader.readAsBinaryString(file);
        reader.readAsDataURL(file);
    }
  }
  
  _handleReaderLoaded(readerEvt) {
   
     var binaryString = readerEvt.target.result;

       this.ImageUpload= binaryString;
    
            console.log("Image Upload",this.ImageUpload);
     }
 //end of image upload

//validating the form before saving
      validate()
      {

           
            var region =  $('#region').val();
            var suburb =  $('#suburb').val();
            var payNumber =  $('#payNumber').val();
            var idNumber =  $('#idNumber').val();
            var fullname =  $('#fullname').val();
            var occupation =  $('#occupation').val();
            var password =  $('#password').val();
            var confirmPassword =  $('#confirmPassword').val();
            var email =  $('#email').val();
            var mobileno =  $('#mobileno').val();
            var address =  $('#address').val();

                     
                  if (region == null || region== "") {
                     alert("Region must be Selected");
                     return false;
                  }else {
                   if (suburb == null || suburb == "") {
                           alert("Suburb must be filled out");
                            return false;
                        }else {
                   if (payNumber == null || payNumber == "") {
                           alert("Pay Number must be filled out");
                            return false;
                        }else{
                        if (idNumber== null || idNumber == "") {
                               alert("Id Number must be filled out");
                                return false;
                            }else{
                            if (fullname== null || fullname == "") {
                               alert("fullname must be filled out");
                                return false;
                            }else{
                            if (occupation== null || occupation == "") {
                               alert("Occupation must be filled out");
                                return false;
                            }else{
                            if (password== null || password == "") {
                               alert("Password must be filled out");
                                return false;
                            }else{
                            if (confirmPassword== null || confirmPassword == "") {
                               alert("Confirm Password must be filled out");
                                return false;
                            }else{
                            if (email== null || email == "") {
                               alert("Email must be filled out");
                                return false;
                            }else{
                            if (mobileno== null || mobileno == "") {
                               alert("Mobile Number must be filled out");
                                return false;
                            }else{
                            if (address== null || address == "") {
                               alert("Work Address must be filled out");
                                return false;
                            }else{
                               return true;
                            }
                            }
                            }
                            }
                            }
                            }
                            }
                            }
                            }
                          }
                            }
             
      }



}





