import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { AddNotificationService } from './addalerts.service';
import { Http, Response, Headers } from '@angular/http';
import { Observable } from 'rxjs/Observable';

declare var $:any;
@Component({
  selector: 'app-addalerts',
  templateUrl: './addalerts.component.html',
  styleUrls: ['./addalerts.component.css'],
   providers: [ AddNotificationService ]
})

export class AddalertsComponent implements OnInit {
  
  public siteurl = window.location.origin;
  public currentUser;
  public suburbName;
 // public suburb;
  public ImageUpload:String="";
  datetimenow = new Date().toLocaleString();

  constructor(private _addService: AddNotificationService,private location: Location) { this.getCurrentUsers(); this.getSuburbs();}

  ngOnInit() 
  { 
     
  }

  ngAfterViewInit(){
    //  Activate the tooltips
    $('[rel="tooltip"]').tooltip();
  }
 
  // dropdown values for suburbs
   getSuburbs(){
 
         this._addService.getSuburbs().subscribe(
           data =>{
             this.suburbName = data.json();
            console.log("Suburbs", this.suburbName);

          });
    }

   //form data variable
    public formModel={};

    saveNotifications()
    {

        if(this.validate())
         { 
             var suburb =  $('#suburb').val();
            $('button[type=submit]').attr('disabled', 'disabled');
          
                    Object.assign(this.formModel, {"sentBy":this.currentUser}, {"capturedBy":this.currentUser},{"image":this.ImageUpload});
                    
                    this._addService.postNotifications(this.formModel)
                    .subscribe(
                          (response: Response)=> {
                            alert("Notification has been stored successfully"),
                              console.log("the response is:",response);
                              this.clearFormData();
                              this.closeNotifications();
                         },
                          (error) => { 
                             if (error.status == 401) {
                                   alert("Authentication reguired");
                              } else if(error.status == 422) {
                                     alert("Notification not stored, please add the image.");
                                    // this.clearFormData();
                                     console.log("the error is:",error);
                                     console.log("Form Data", this.formModel)
                              } else{
                                     alert("Notification data is not stored");
                                   //  this.clearFormData();
                                     console.log("the error is:",error);
                                     console.log("Form Data", this.formModel);

                                  }
                          });

              // }
          }
            
      }





    getCurrentUsers()  
     {  
        let data = this._addService.getUserDetails().subscribe(( response: Response) => {
          
        const str = response.json(); 
        
        this.currentUser = str.fullname;
        console.log("User details:",this.currentUser)

      });  
         
    }

      
    //Clear form fields
    clearFormData(){
      
       $('input[type="text"]').val('');
       $('#message').val('');
       $('#dateCaptured').val('');
       $('#sentBy').val('');
    }

    //Close form and redirect
    closeNotifications(){this.location.back(); }


      
//image upload function 
handleFileSelect(evt){
      var files = evt.target.files;
      var file = files[0];
    
    if (files && file) {
        var reader = new FileReader();

        reader.onload =this._handleReaderLoaded.bind(this);

       // reader.readAsBinaryString(file);
         reader.readAsDataURL(file);
    }
  }
  
  _handleReaderLoaded(readerEvt) {
     var binaryString = readerEvt.target.result;
            //this.ImageUpload= btoa(binaryString);
            this.ImageUpload= binaryString;
           // console.log(btoa(binaryString));
    }

 //end of image upload
 validate()
      {

            // var suburb =  $('#suburb').val();
            var title =  $('#title').val();
            var message =  $('#message').val();
            var fileinput =  $('#fileinput').val();
                             
                  
              // if (suburb == null || suburb== "") {
              //        alert("Suburb must be filled out");
              //        return false;
              //   }else {
                if (title == null || title== "") {
                     alert("Title must be filled out");
                     return false;
                  }else {
                   if (message == null || message == "") {
                           alert("Message must be filled out");
                            return false;
                        }else{
                        if (fileinput== null || fileinput == "") {
                               alert("Image must be uploaded");
                                return false;
                            }else{
                               return true;
                            }
                            }
                            }
                            // }
                           
                                                       
             
      }




}


