import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';


@Injectable()
export class EditUserService 
{
	 constructor(private http: Http) {}  

   
     public getUsersById(userId: any)  
     {     
     	  let token = JSON.parse(localStorage.getItem('storedToken'));  
          let url: string = 'https://tshwanesafetyapi.herokuapp.com/api/TshwaneUsers/'+ userId +'?filter={"include":["user_region","user_suburb"]}&access_token='+token;
          return this.http.get(url);
        
    }

     public getRegion()
    {
        
        let url: string = 'https://tshwanesafetyapi.herokuapp.com/api/Regions?filter[order]=name%20ASC';
        return this.http.get(url);
    }

      public putUsers(id: any,userData: any)
    {
        let userDetail = localStorage.getItem('currentUser');
        let access_token = JSON.parse(userDetail).id;

        let headers = new Headers({'Content-Type': 'application/json','access-control-allow-origin': 'http://localhost:4200' });
        let options = new RequestOptions({ headers: headers });
        let body = JSON.stringify(userData);

        return this.http.patch('https://tshwanesafetyapi.herokuapp.com/api/TshwaneUsers/'+id+'?access_token='+access_token, body , options).map((res: Response) => res.json());

    }
     public getSuburbs()
    {
        
        let url: string = 'https://tshwanesafetyapi.herokuapp.com/api/Suburbs?filter={"where": {"name": {"nin": ["All"]}}}&[order]=name ASC';
        return this.http.get(url);
    }
    
}





