import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { AddEnquiryService } from './addenquiry.service';
import { Http, Response, Headers } from '@angular/http';
import { Observable } from 'rxjs/Observable';
//import { Ng2MessagePopupComponent, Ng2PopupComponent} from 'ng2-popup';

declare var $:any;

@Component({
  selector: 'app-addenquiry',
  templateUrl: './addenquiry.component.html',
  styleUrls: ['./addenquiry.component.css'],
  providers: [ AddEnquiryService ]//,
 // directives: [Ng2PopupComponent],
})

export class AddenquiryComponent implements OnInit 
{
	  public siteurl = window.location.origin;
    enquiryId: string;
   	enquiryName='';
    
    public currentUser;
         

    constructor(private _addService: AddEnquiryService, private location: Location) { }

	  ngOnInit() {this.enquiryType();this.getCurrentUsers();
      //this.dateFunc();
    }	

	  //get all the data from the API
     public enquiryType(){
      let data = this._addService.getEnquiryType().subscribe(( response: Response) => {
          
        const str = response.json(); 
        
        this.enquiryName = str;
        console.log("Enquiry Types:",this.enquiryName)

      });
    }
    
    ngAfterViewInit(){
      //  Activate the tooltips
      $('[rel="tooltip"]').tooltip();
    }
 

   //form data variable
    public formModel: any ={};

    saveEnquiries(){

       if(this.validate())
        { 
       
         $('button[type=submit]').attr('disabled', 'disabled');
          
         //pushing objects in the list
          Object.assign(this.formModel, {"capturedBy":this.currentUser});
          
          this._addService.postEnquiries(this.formModel)
          .subscribe(
                (response: Response)=> {
                  alert("Enquiry has been stored successfully"),
                  console.log("the response is:",response),
                    this.clearFormData();
                    this.closeEnquiries();
                },
                (error) => { 
                      if (error.status == 401) {
                          alert("Authentication reguired");
                        } else {
                           alert("Enquiry data is not stored");
                           //this.clearFormData();
                           console.log("the error is:",error)
                           //console.log("Form Data", this.formModel);
                        }
                }
      
          )
       }  
    }


    getCurrentUsers()  
     {  
        let data = this._addService.getUserDetails().subscribe(( response: Response) => {
          
        const str = response.json(); 
        
        this.currentUser = str.fullname;
        console.log("User details:",this.currentUser)

      });  
         
    }
    

    //Clear form fields
    clearFormData(){
      
       $('input[type="text"]').val('');
       $('input[type="number"]').val('');
       $('#notes').val('');
       $('#type').val('');
       $('#selectedEnquiry').val('');
       $('#dateCaptured').val('');
       $('#capturedBy').val('');
    }

    //Close form and redirect
    closeEnquiries(){
      this.location.back();
      
    }


    //validating the form before saving
      validate()
      {

           
            var type =  $('#type').val();
            var fullNames =  $('#fullNames').val();
            var contactNumber =  $('#contactNumber').val();
            var address =  $('#address').val();
            var notes =  $('#notes').val();
            
                     
                  if (type == null || type== "") {
                     alert("Enquiry Type must be Selected");
                     return false;
                  }else {
                   if (fullNames == null || fullNames == "") {
                           alert("Name and Surname must be filled out");
                            return false;
                        }else{
                        if (contactNumber== null || contactNumber == "") {
                               alert("Contact Number must be filled out");
                                return false;
                            }else{
                            if (address== null || address == "") {
                               alert("Address must be filled out");
                                return false;
                            }else{
                            if (notes== null || notes == "") {
                               alert("Notes must be filled out");
                                return false;
                            }else{
                               return true;
                            }
                            }
                            }
                            }
                            }
                                                       
             
      }


}



