import { Component, OnInit, OnChanges, AfterViewInit } from '@angular/core';
import { Http, Response, Headers } from '@angular/http';
import { Angular2Csv } from 'angular2-csv/Angular2-csv';
import { ViewIncidentsService } from './viewincidents.service';
import * as moment from 'moment';

declare var $:any;
declare const google: any;
declare var swal:any;

@Component({
  selector: 'app-viewincidents',
  templateUrl: './viewincidents.component.html',
  styleUrls: ['./viewincidents.component.css'],
  providers: [ ViewIncidentsService ]
})

export class ViewincidentsComponent implements OnInit {

    public selectedIncidentsId; public category; public type; public region; public channel;
    public location; public reportedby; public dateupdated_history; public mobileno; public status;
    public status_history; public capturedBy; public reportedOn; public description; public refNumber;
    public image; public refnum; public index;public i; public selectedHistoryId;
    public lat:number; public lot:number;

    public data; public dataCategory;
    public filterQuery = ""; public rowsOnPage = 10;
    public sortBy = "dateCaptured"; public sortOrder = "desc";

    constructor( private viewincidentservice: ViewIncidentsService, private http: Http)
    {
       // if (window.location.href.indexOf('reload')==-1) {
       //   window.location.replace(window.location.href+'?reload');
       // }
       this.getIncidentSelectedId(); this.getIncidentById(); this.getHistoryIncidentById();
    }

    ngOnInit(){




    }
     ngAfterViewInit(){



     }


     getIncidentSelectedId() {

        // get Selected Id from the Datatable
        let IncidentSelectedId = JSON.parse(localStorage.getItem('selectedIncidentId'));
        this.selectedIncidentsId = IncidentSelectedId;
        this.refnum = JSON.parse(localStorage.getItem('selectedRefNumber'));
        console.log("incident Id:",this.selectedIncidentsId);

    }

    getIncidentById()
    {
         let data = this.viewincidentservice.getIncidentById(this.selectedIncidentsId).subscribe(( response: Response) => {

         const str = response.json();
         this.category = str.incident_category.name;
         this.type = str.incident_subcategory.name;
         this.region = str.incident_region.name;
         this.channel = str.incident_channel.name;
         this.lat = str.lat;
         this.lot = str.lot;

         this.reportedby = str.reportedBy;
         this.mobileno = str.mobileno;
         this.status = str.incident_status.name;
         this.capturedBy = str.capturedBy;
         this.reportedOn = str.reportedOn;
         this.description = str.description;
         this.refNumber = str.refNumber;

          this.image = str.images;
          // console.log("image",  this.image );
         if(this.image  == undefined){
            this.image ="../src/assets/img/image_placeholder.jpg";
         }else{
            this.image = str.images.imageString;
         }

        var loc = str.location;
        console.log("location",loc);
        if(loc == null ){
              // var lat = str.lat;
              // var lot = str.lot;
              // var results = 'https://maps.googleapis.com/maps/api/geocode/json?latlng='+lat+','+lot+'&key=AIzaSyCUENRI7Fj17ZAMV3mlOwJeNh2fy8EPsXg'
              // return this.http.get(results);
                // console.log("map results",results.);
             // return results

           // this.location = ;
              // alert(results.);



        }else{
             this.location = str.location;
        }


        if( this.status == "Closed"){
            document.getElementById("update").style.display="none";

         }
         this.initialize();
          console.log("incident By Id Data", str);

      });





    }

    // map(){

    //     var map = new google.maps.Map(document.getElementById('viewmap'), {
    //     center: {lat: -25.7487047, lng: 28.268015399999967},
    //     zoom: 13,
    //     streetViewControl: true,
    //     scaleControl: true,
    //     rotateControl: true,
    //     panControl: true,
    //     mapTypeControl: true,
    //     mapTypeControlOptions: {
    //       style: google.maps.MapTypeControlStyle.DROPDOWN_MENU,
    //       position: google.maps.ControlPosition.TOP_RIGHT
    //     },
    //     overviewMapControl: true,
    //     overviewMapControlOptions: {
    //       opened: true,
    //       position: google.maps.ControlPosition.TOP_LEFT
    //     },
    //     mapTypeId: 'roadmap'
    //   });
    // }
    initialize(){
        console.log("Lat",this.lat);
        console.log("Lot",this.lot);
        var myLatlng = new google.maps.LatLng(this.lat,this.lot);
        var myOptions = {
            zoom: 14,
            streetViewControl: true,
            scaleControl: true,
            rotateControl: true,
            panControl: true,
            mapTypeControl: true,
            center: myLatlng,
            overviewMapControl: true,
                overviewMapControlOptions: {
                  opened: true,
                  position: google.maps.ControlPosition.TOP_LEFT
                },
            mapTypeId: google.maps.MapTypeId.ROADMAP
            }
        var map = new google.maps.Map(document.getElementById("viewmap"), myOptions);
         var marker = new google.maps.Marker({
             position: myLatlng,
             map: map,
         title:"The Incident Occured Here"
        });
    }

    getHistoryIncidentById()

    {
         console.log("History ref number", this.refnum );
         let data = this.viewincidentservice.getHistoryIncidentById( this.refnum).subscribe(( response: Response) => {
             const str = response.json();

             this.data = str;

             console.log("History Data 1", this.data );

         });
    }


    onClick()
    {

        // gets the row id of the record
         $('#datatables tr').click(function() {

            var $row = $(this).closest("tr"),        // Finds the closest row <tr>
                $tds = $row.find("td:nth-child(1)"); // Finds the 1nd <td> element
            $.each($tds, function() {                // Visits every single <td> element

              this.selectedHistoryId = $(this).text();
               // console.log("History selected id:", this.selectedHistoryId );
              localStorage.setItem('selectedHistoryId', JSON.stringify(this.selectedHistoryId));
              // console.log("selectedHistoryId", JSON.stringify(this.selectedHistoryId)); // Prints out the text within the <td>

            });
         });

    }





}



