import { Routes } from '@angular/router';

import { ExtendedFormsComponent } from './extendedforms/extendedforms.component';
import { RegularFormsComponent } from './regularforms/regularforms.component';
import { ValidationFormsComponent } from './validationforms/validationforms.component';
import { WizardComponent } from './wizard/wizard.component';
import { AddincidentsComponent } from './addincidents/addincidents.component';
import { ViewincidentsComponent } from './viewincidents/viewincidents.component';
import { ChangestatusComponent } from './changestatus/changestatus.component';
import { HistoryComponent } from './history/history.component';
import { ViewhistoryComponent } from './viewhistory/viewhistory.component';
import { AddusersComponent } from './addusers/addusers.component';
import { EditusersComponent } from './editusers/editusers.component';
import { ViewusersComponent } from './viewusers/viewusers.component';
import { ViewenquiryComponent } from './viewenquiry/viewenquiry.component';
import { AddenquiryComponent } from './addenquiry/addenquiry.component';
import { AddalertsComponent } from './addalerts/addalerts.component';
import { ViewalertsComponent } from './viewalerts/viewalerts.component';
import { EditalertsComponent } from './editalerts/editalerts.component';

export const FormsRoutes: Routes = [
    {
      path: '',
      children: [ {
        path: 'regular',
        component: RegularFormsComponent
    }]},{
    path: '',
    children: [ {
      path: 'extended',
      component: ExtendedFormsComponent
    }]
    },{
    path: '',
    children: [ {
      path: 'validation',
      component: ValidationFormsComponent
    }]
    },{
        path: '',
        children: [ {
            path: 'wizard',
            component: WizardComponent
    }]
    },{
    path: '',
    children: [ {
      path: 'addincidents',
      component: AddincidentsComponent
    }]
    },{
    path: '',
    children: [ {
      path: 'viewincidents',
      component: ViewincidentsComponent
    }]
    },{
    path: '',
    children: [ {
      path: 'changestatus',
      component: ChangestatusComponent
    }]
    },{
    path: '',
    children: [ {
      path: 'history',
      component: HistoryComponent
    }]
    },{
      path: '',
      children: [ {
        path: 'viewhistory',
        component: ViewhistoryComponent
      }]
    },{
    path: '',
    children: [ {
      path: 'addusers',
      component: AddusersComponent
    }]
    },{
      path: '',
      children: [ {
        path: 'editusers',
        component: EditusersComponent
      }]
    },{
      path: '',
      children: [ {
        path: 'viewusers',
        component: ViewusersComponent
      }]
    },{
      path: '',
      children: [ {
        path: 'viewenquiry',
        component: ViewenquiryComponent
      }]
    },{
      path: '',
      children: [ {
        path: 'addenquiry',
        component: AddenquiryComponent
      }]
    },{
      path: '',
      children: [ {
        path: 'addalerts',
        component: AddalertsComponent
      }]
    },{
      path: '',
      children: [ {
        path: 'viewalerts',
        component: ViewalertsComponent
      }]
    },{
      path: '',
      children: [ {
        path: 'editalerts',
        component: EditalertsComponent
      }]
    }
];



